import React, { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
// import Modal from "react-modal";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import { getPlatforms } from "utils/api";
import styles from "./add-game-modal.module.scss";
import Select from "react-select";

// bind modal to app element
// Modal.setAppElement("#root");
const Modal = withReactContent(Swal);

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    width: "500px",
    height: "400px",
  },
};

export default function AddGameModal({ isOpen, closeModal, submitGame }) {
  const [platforms, setPlatforms] = useState([]);

  useEffect(() => {
    getPlatforms().then((response) => {
      setPlatforms(response.data.results);
    });
  }, []);

  const {
    register,
    control,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm();

  function onSubmit(game) {
    // format platforms data, submit new game and close modal
    game.platforms = game.platforms?.map((platform) => ({ platform: platform }));
    game.isUserAdded = true;
    // create slug from name, replacing white space
    game.slug = game.name.replace(/\s/g, "-");
    submitGame(game);
    closeModal();
    // Modal.close();
  }

  useEffect(() => {
    if (isOpen) {
      Modal.fire({
        // confirmButtonText: "Submit",
        showConfirmButton: false,
        showCloseButton: true,
        title: <p>Add new game</p>,
        html: <Form />,
        didOpen: () => {
          // `MySwal` is a subclass of `Swal` with all the same instance & static methods
          // MySwal.showLoading();
        },
      }).then((result) => {
        // return MySwal.fire(<p>Shorthand works too</p>);
      });
    } else {
      Modal.close();
    }
  }, [isOpen]);

  function Form() {
    return (
      <div className={styles.addGameModal}>
        {/* <button onClick={closeModal} className={styles.closeBtn}>
          close
        </button> */}

        <form onSubmit={handleSubmit(onSubmit)}>
          {/* name */}
          {/* register your input into the hook by invoking the "register" function */}
          <input {...register("name", { required: true })} />
          {/* errors will return when field validation fails  */}
          {errors?.name && <span>Required</span>}

          {/* image */}
          <input
            defaultValue="https://www.theedgesusu.co.uk/wp-content/uploads/2017/09/Super-Mario-Sunshine.jpg"
            {...register("background_image", { required: true })}
          />
          {errors?.background_image && <span>Required</span>}

          {/* platforms */}
          {platforms && (
            <>
              <Controller
                render={({ field: { onChange } }) => (
                  <Select
                    isMulti
                    options={platforms.map((platform) => ({
                      value: platform.name,
                      label: platform.name,
                      name: platform.name,
                    }))}
                    onChange={onChange}
                  />
                )}
                control={control}
                name="platforms"
                register={register}
                setValue={setValue}
                rules={{ required: true }}
              />

              {errors?.platforms && <span>Required</span>}
            </>
          )}
          <input type="submit" />
        </form>
      </div>
    );
  }

  return null;
}
